@import url('https://fonts.googleapis.com/css2?family=Grand+Hotel&display=swap');


a{
  color:black !important;
}

.brand-logo,h2{
  font-family: 'Grand Hotel', cursive;

}
.mycard{
  margin-top: 30px;
  
}
.auth-card{
  padding: 40px 50px;
  text-align: center;
  max-width: 500px;
  margin: 10px auto;
}
.input-field input[type=text]:focus {
  border-bottom: 1px solid #1eb58d !important;
  box-shadow: 0 1px 0 0 #1eb58d !important;
}
.gallery{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  
}

.item{
  width:30%
}


.home-card{
  max-width:500px;
  height: max-content;
  margin:26px auto;
}

#heart{
  color:red;
}

#toast-container{
  top:30px !important;
  right:20px !important;
  left: auto !important;
}

.material-icons:hover{
  cursor:pointer;
}
